
.allrecord{
		font-size: 1.6vw;
}
.allrecord div.head{
			position: absolute;
    	top: 0rem;
    	left: 230px;
}
.allrecord div.head .back{
		top: 50%;
		left: 0;
    -webkit-transform: translateY(-45%);
    transform: translateY(-45%);
}
input,select{
		font-size: 16px;
}
.cov-date-body{
}
.allrecord-head{
		line-height: 81px;
		padding-left: 380px;
}
.allrecord-head-left{
    margin-left: 20px;
    margin-right: 10px;
}
.allrecord-head>div {
    display: inline-block;
    margin-right: 20px;
}
.allrecord .name-text{
		display: inline-block;
    height: 40px;
    margin-left: 14px;
}
.allrecord .name-text input{
		height: 40px;
    width: 150px;
		padding-left: 10px;
    border: 1px solid #999;
}
.allrecord .time,.allrecord .status-text{
		display: inline-block;
		margin-left: 14px;
}
.status-select{
		outline: none;
    border: 0;
    background: #fff;
    border: 1px solid #999;
    height: 40px;
    width: 150px;
		padding-left: 10px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
.allrecord-content{
    padding: 0px 1%;
    padding-left: 230px;
    padding-right: 30px;
}
.allrecord-content-head{
		background: #f5faff;
    line-height: 60px;
    border-radius: 5px;
}
.allrecord-list{
		padding-top: 10px;
		overflow: auto;
}
.allrecord-content-head>div,.allrecord-item>div {
	    display: inline-block;
	    text-align: center;
	    width: 18%;
}
.allrecord-item .make-operate{
	 	width: 10%;
}
.allrecord-item{
	 		line-height: 60px;
	 		font-size: 1.5vw;
	 		padding: 8px 0;
}
.make-operate span{
	 		display: inline-block;
	    background: #fc9453;
	    line-height: 60px;
	    width: 75%;
	    color: #fff;
	    border-radius: 5px;
}
.make-status.done{
	 	color: #3C9BFF;
}
.make-operate.done span{
	 		background: #fc9453;
}
.make-statu.unfinished{
	 	 color: #d43c33;
}
.make-operate.unfinished span{
	 		background: #3C9BFF;
}
.make-status .Finish{
	 	color: #3C9BFF;
}
.make-status .Pending{
	 	color: #d43c33;
}
.make-status .Canceled{
	 	color: #d43c33;
}
.make-operate .Pending{
	 		background:#3C9BFF;
}
.make-operate .Pending.overdue{
	 		background:#eee;
	 		color: #999;
}
.cov-date-caption{
	 	color:#3C9BFF !important;
}
.checked{
	 	background:#3C9BFF !important;
}
.cov-datepicker{
	 	border-color:#999 !important;
	 	padding: 0 0 0 10px !important;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
	 	width: 150px !important;
	 	height: 40px !important;
	 	line-height: 40px !important;
}
.cov-date-caption{
	 	font-size: 2vw !important;
	 	line-height: 1;
}
.cov-date-body,.cov-picker-box{
	 	width: 647px !important;
}
.cov-picker-box{
	 	height: 522px !important;
}
.day{
	 	width: 65px !important;
	 	height: 65px !important;
    line-height: 65px !important;
    font-size: 23px;
}
